// Tabs
.uf-tab {
	&-wrapper {
		border-bottom: 1px solid #ccc;
		font-size: 0;
		padding-top: 3px;
		width: 100%;
		position: relative;
		z-index: 2;
		margin-bottom: 10px;
	}

	display: inline-block;
	background: #f0f0f0;
	font-size: 11px;
	font-weight: bold;
	line-height: 34px;
	text-decoration: none;
	vertical-align: top;
	padding: 0 10px;
	border: 1px solid #ccc;
	margin-bottom: -1px;
	margin-right: 6px;
	color: #444;

	.uf-boxed-fields & {
		border-color: #ddd;
	}

	&:focus {
		box-shadow: none;
	}

	&:first-child {
		margin-left: 10px;
	}

	&:hover {
		background: #fff;
		color: #222;
	}

	&-active {
		color: #000;
		background: none;
		border-bottom-color: #f1f1f1;
	}

	&.disabled {
		color: #bbb;
		background: #f9f9f9;
		cursor: default;
	}

	&-hidden {
		display: none;
	}

	&-invalid {
		background: $required;
		color: #fff;
	}

	&-invalid.uf-tab-active {
		background: #fff;
		color: $required;
	}

	&-text,
	&-icon {
		display: inline-block;
		vertical-align: middle;
	}

	&-icon {
		margin-right: 5px;
	}
}

.uf-boxed-fields .uf-tab {
	&-wrapper {
		background: #eee;
		border-bottom-color: #ddd;
		margin-bottom: 0;;
	}

	border-color: #ddd;

	&-active {
		background: #fff;
		border-bottom-color: #fff;
	}
}
